<template lang='pug'>
  .page-container
    .page-top-tip {{$route.query.tname}}
    .page-operatebar
      el-button(type='primary' @click='isDialogShow = true') 
    el-table(:data='list' border stripe)
      el-table-column(type='index')
      el-table-column(label='' prop='dname')
      el-table-column(label='' prop='unitPrice')
      el-table-column(label='' prop='receiverUnit')
      el-table-column(label='' prop='')
        template(slot-scope='{row}')
          el-button(type='text' @click='delPlatform(row)') 
    el-dialog(:visible.sync='isDialogShow' title='' width='50%')
      el-form(label-width='120px' label-position='right' :model='form' :rules='rules' ref='form')
        el-form-item(label=':' prop='taskTypeName')
          el-input(placeholder='' v-model='form.taskTypeName' clearable).w300
        el-form-item(label='' prop='unitPrice')
          el-input(placeholder='' v-model='form.unitPrice' clearable).w300
        el-form-item(label='' prop='receiverUnit')
          el-input(placeholder='' v-model='form.receiverUnit' clearable).w300
      .p-foot(slot='footer')
        el-button(type='primary' @click='submit') 
        el-button(@click='isDialogShow=false') 
</template>
<script>
export default {
  data() {
    return {
      ENV,
      form: {
        taskTypeName: '',
        unitPrice: '',
        receiverUnit: '',
        fatherTaskType: ''
      },
      rules: {
        taskTypeName: [
          {required: true, message: ''}
        ],
        unitPrice: [
          {required: true, message: ''}
        ],
        receiverUnit: [
          {required: true, message: ''}
        ]
      },
      isDialogShow: false,
      list: [],
    }
  },
  methods: {
    delPlatform(row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const res = await API.mission.delSecondTaskType({
          fatherTaskType: this.form.fatherTaskType,
          taskTypeId: row.did
        })

        if(res.data.code === 100) {
          this.$message({
            type: 'success',
            message: '!'
          });
          this.getAllSecondTaskType()
        }
      })
    },
    submit() {
      this.$refs.form.validate(async valid => {
        if(valid) {
          const res = await API.mission.addSecondTaskType(this.form)

          if(res.data.code === 100) {
            this.$message.success('')
            this.isDialogShow = false
            this.form = {
              taskTypeName: '',
              unitPrice: '',
              receiverUnit: '',
              fatherTaskType: this.$route.query.fatherTaskType
            }
            this.getAllSecondTaskType()
          }
        }
      })
    },
    async getAllSecondTaskType() {
      this.form.fatherTaskType = this.$route.query.fatherTaskType

      const res = await API.mission.getAllSecondTaskType({
        fatherTaskType: this.form.fatherTaskType
      })

      if(res.data.code === 100) {
        this.list = res.data.data.taskTypeList
      }
    }
  },
  mounted() {
    this.getAllSecondTaskType()
  }
}
</script>
