<template lang='pug'>
  .page-container
    .page-top-tip {{$route.query.pname}}
    .page-operatebar
      el-button(type='primary' @click='isDialogShow = true') 
    el-table(:data='list' border stripe)
      el-table-column(type='index')
      el-table-column(label='	' prop='tname')
      el-table-column(label='' prop='tunit')
      el-table-column(label='' prop='receiverUnit')
      el-table-column(label='' prop='')
        template(slot-scope='{row}')
          el-button(type='text' @click='$router.push({name: "childCategoryList", query: {fatherTaskType: row.tid, tname: row.tname}})') 
          el-button(type='text' @click='delPlatform(row)') 
    el-dialog(:visible.sync='isDialogShow' title='' width='50%')
      el-form(label-width='120px' label-position='right' :model='form' :rules='rules' ref='form')
        el-form-item(label=':' prop='taskTypeName')
          el-input(placeholder='' v-model='form.taskTypeName' clearable).w300
        el-form-item(label='' prop='taskTypeUnit')
          el-input(placeholder='' v-model='form.taskTypeUnit' clearable).w300
        el-form-item(label='' prop='receiverUnit')
          el-input(placeholder='' v-model='form.receiverUnit' clearable).w300
      .p-foot(slot='footer')
        el-button(type='primary' @click='submit') 
        el-button(@click='isDialogShow=false') 
</template>
<script>
export default {
  data() {
    return {
      ENV,
      form: {
        taskTypeName: '',
        taskTypeUnit: '',
        receiverUnit: '',
        taskTypePlatform: ''
      },
      rules: {
        taskTypeName: [
          {required: true, message: ''}
        ],
        taskTypeUnit: [
          {required: true, message: ''}
        ],
        receiverUnit: [
          {required: true, message: ''}
        ]
      },
      fileList: [],
      isDialogShow: false,
      isDialogShow1: false,
      form1: {
        platformName: '',
        platformId: ''
      },
      list: [],
      action: `${ENV}/Task/uploadMedia`,
    }
  },
  methods: {
    delPlatform(row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const res = await API.mission.delPC({
          taskTypeId: row.tid
        })

        if(res.data.code === 100) {
          this.$message({
            type: 'success',
            message: '!'
          });
          this.getPCList()
        }
      })
    },
    submit() {
      this.$refs.form.validate(async valid => {
        if(valid) {
          const res = await API.mission.addPC(this.form)

          if(res.data.code === 100) {
            this.$message.success('')
            this.isDialogShow = false
            this.form = {
              taskTypeName: '',
              taskTypeUnit: '',
              receiverUnit: '',
              taskTypePlatform: this.$route.query.platformId
            }
            this.getPCList()
          }
        }
      })
    },
    async getPCList() {
      this.form.taskTypePlatform = this.$route.query.platformId

      const res = await API.mission.getPCList({
        platformId: this.form.taskTypePlatform
      })

      if(res.data.code === 100) {
        this.list = res.data.data.taskTypeList
      }
    }
  },
  mounted() {
    this.getPCList()
  }
}
</script>
