<template lang="pug">
  .page-container
    .page-operatebar
      el-button(type='primary' @click='isDialogShow = true') 
    el-table(:data='list' border stripe).page-table
      el-table-column(type='index')
      el-table-column(label='' prop='nid')
      el-table-column(label='' prop='ntitle')
      el-table-column(label='' prop='ncontent')
      el-table-column(label='' prop='ndate')
      el-table-column(label='' prop='adminName')
    el-dialog(:visible.sync='isDialogShow' title='' width='40%')
      el-form(label-width='100px' label-position='right' ref='form' :model='dForm' :rules='rules')
        el-form-item(label='' prop='noticeTitle')
          el-input(placeholder='' v-model='dForm.noticeTitle' clearable).w300
        el-form-item(label='' prop='noticeContent')
          el-input(placeholder='' v-model='dForm.noticeContent' type='textarea' clearable :rows='6').w300
      .p-foot(slot='footer')
        el-button(type='primary' @click='submit') 
        el-button(@click='isDialogShow=false') 
</template>

<script>
import mixin from '@/mixin'

export default {
  mixins: [mixin],
  data() {
    return {
      form: {
        page: 1
      },
      dForm: {
        noticeTitle: '',
        noticeContent: ''
      },
      rules: {
        noticeTitle: [
          {required: true, message: ''}
        ],
        noticeContent: [
          {required: true, message: ''}
        ]
      },
      isDialogShow: false,
      pagination: {
        totalCount: 1,
        pageSize: 10
      },
      list: []
    }
  },
  methods: {
    submit() {
      this.$refs.form.validate(async valid => {
        if(valid) {
          const res = await API.notice.add(this.dForm)

          if(res.data.code === 100) {
            this.$message.success('')
            this.isDialogShow = false
            this.$refs.form.resetFields()
            this.getList()
          }
        }
      })
    },
    async getList() {
      const {page} = this.$route.query

      this.form.page = +page || 1
      
      const params = {...this.form, pageSize: this.pagination.pageSize}

      const res = await API.notice.searchAll(params)

      if(res.data.code === 100) {
        this.list = res.data.data.list
        this.pagination.totalCount = res.data.data.total
      }
    }
  }
}
</script>