<template lang='pug'>
  .page-container
    .page-operatebar
      el-button(type='primary' @click='create') 
    el-table(:data='list' border stripe)
      el-table-column(type='index')
      el-table-column(label='' prop='pid')
      el-table-column(label='' prop='pname')
      el-table-column(label='logo')
        template(slot-scope='{row}')
          img(:src='`${ENV}${row.plogo}`' width='30' style='vertical-align: middle')
      el-table-column(label='' prop='')
        template(slot-scope='{row}')
          el-button(type='text' @click='$router.push({name: "categoryList", query: {platformId: row.pid, pname: row.pname}})') 
          el-button(type='text' @click='editName(row)') 
          el-button(type='text' @click='delPlatform(row)') 
    el-dialog(:visible.sync='isDialogShow1' title='' width='50%')
      el-form(label-width='120px' label-position='right')
        el-form-item(label='' prop='')
          el-input(placeholder='' v-model='form1.platformName' clearable).w300
      .p-foot(slot='footer')
        el-button(type='primary' @click='edit') 
        el-button(@click='isDialogShow1=false') 
    el-dialog(:visible.sync='isDialogShow' title='' width='50%')
      el-form(label-width='120px' label-position='right' :model='form' :rules='rules' ref='form')
        el-form-item(label=':' prop='platformName')
          el-input(placeholder='' v-model='form.platformName' clearable).w300
        el-form-item(label='' prop='taskAccountLimit')
          el-input(placeholder='' v-model='form.taskAccountLimit' clearable).w300
        el-form-item(label='' prop='')
          el-select(placeholder='' v-model='form.taskAccountType').w300
            el-option(value='1' label='uid')
            el-option(value='2' label='')
        el-form-item(label='logo:' prop='file')
          el-upload(:auto-upload="false" :action='action' list-type='picture-card' :limit='1' :on-change='handleChange' :on-remove="handleRemove")
            <i class="el-icon-plus"></i>
            .upload-tip(slot='tip') jpg/png500kb
      .p-foot(slot='footer')
        el-button(type='primary' @click='submit') 
        el-button(@click='isDialogShow=false') 
</template>
<script>
export default {
  data() {
    return {
      ENV,
      form: {
        platformName: '',
        file: null,
        taskAccountLimit: '',
        taskAccountType: '1'
      },
      rules: {
        platformName: [
          {required: true, message: ''}
        ],
        taskAccountLimit: [
          {required: true, message: ''}
        ],
        file: [
          {required: true, message: 'logo'}
        ]
      },
      fileList: [],
      isDialogShow: false,
      isDialogShow1: false,
      form1: {
        platformName: '',
        platformId: ''
      },
      list: [],
      action: `${ENV}/Task/uploadMedia`,
    }
  },
  methods: {
    editName(row) {
      this.isDialogShow1 = true
      this.form1.platformName = row.pname
      this.form1.platformId = row.pid
    },
    async edit() {
      const res = await API.mission.editPlatform(this.form1)

      if(res.data.code === 100) {
        this.$message.success('')
        this.isDialogShow1 = false
        this.getPlatformList()
      }
    },
    delPlatform(row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const res = await API.mission.delPlatform({
          platformId: row.pid
        })

        if(res.data.code === 100) {
          this.$message({
            type: 'success',
            message: '!'
          });
          this.getPlatformList()
        }
      })
    },
    submit() {
      this.$refs.form.validate(async valid => {
        if(valid) {
          const res = await API.mission.addPlatform(this.form)

          if(res.data.code === 100) {
            this.$message.success('')
            this.isDialogShow = false
            this.$refs.form.resetFields()
            this.getPlatformList()
          }
        }
      })
    },
    handleChange(file, fileList) {
      console.log('f', file)
      this.form.file = file.raw
    },
    handleRemove() {
      this.form.file = null
    },
    create() {
      this.isDialogShow = true
    },
    async getPlatformList() {
      const res = await API.mission.getPlatformList()

      if(res.data.code === 100) {
        this.list = res.data.data
      }
    }
  },
  mounted() {
    this.getPlatformList()
  }
}
</script>
