<template lang="pug">
  .page-container
    .page-operatebar
      el-button(type='primary' @click='isDialogShow = true') 
    el-table(:data='list' border stripe).page-table
      el-table-column(type='index')
      el-table-column(label='' prop='gname')
      el-table-column(label='')
        template(slot-scope='{row}') {{row.gnum}}/{{row.gmax}}
      el-table-column(label='' prop='')
        template(slot-scope='{row}')
          el-button(type='text' @click='editGroup(row)') 
          el-button(type='text' @click='$router.push({name: "groupDetail", query: {groupId: row.gid}})') 
          el-button(type='text' @click='deleteGroup(row)') 
    el-dialog(:visible.sync='isDialogShow' title='' width='40%')
      el-form(label-width='100px' label-position='right' ref='form' :model='form' :rules='rules')
        el-form-item(label='' prop='groupName')
          el-input(placeholder='' v-model='form.groupName' clearable).w300
      .p-foot(slot='footer')
        el-button(type='primary' @click='submit') 
        el-button(@click='isDialogShow=false') 
</template>

<script>
export default {
  data() {
    return {
      form: {
        groupName: ''
      },
      rules: {
        groupName: [
          {required: true, message: ''}
        ]
      },
      list: [],
      isDialogShow: false,
      groupId: ''
    }
  },
  methods: {
    editGroup(row) {
      this.isDialogShow = true
      this.groupId = row.gid
      this.form.groupName = row.gname
    },
    async edit() {
      const res = await API.group.changeGroupName({
        groupId: this.groupId,
        groupName: this.form.groupName
      })

      if(res.data.code === 100) {
        this.$message.success('')
        this.isDialogShow = false
        this.form.groupName = ''
        this.groupId = ''
        this.groupManagement()
      }
    },
    async submit() {
      this.$refs.form.validate(async valid => {
        if(valid) {
          if(this.groupId !== '') {
            this.edit()
            return
          }
          const res = await API.group.buildGroup(this.form)

          if(res.data.code === 100) {
            this.$message.success('')
            this.isDialogShow = false
            this.form.groupName = ''
            this.$refs.form.clearValidate()
            this.groupManagement()
          }
        }
      })
    },
    deleteGroup(row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const res = await API.group.deleteGroup({
          groupId: row.gid,
          groupNum: row.gnum
        })

        if(res.data.code === 100) {
          this.$message({
            type: 'success',
            message: '!'
          });
          this.groupManagement()
        }
      })
    },
    async groupManagement() {
      const res = await API.group.groupManagement()

      if(res.data.code === 100) {
        this.list = res.data.data.list
      }
    }
  },
  mounted() {
    this.groupManagement()
  }
}
</script>